body {
  margin: 0;
  font-family: 'Righteous', cursive, -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Navbar-logo{
  font-family: 'Righteous', cursive;
  font-size: 30px !important;
}

.nav-bar{
  width: 100%;
  font-family: 'Righteous', cursive;
}

.nav-item{
  margin: auto;
  font-size:larger;
}


#mainInfo > ul li{
  font-family: 'Righteous', cursive;
  display: inline-block;  /*把li點點去掉*/
  list-style: none; /*不要用任何style*/
  font-size: 20px; /*字的大小*/
  margin-top: 0.5em; /*置中在mainInfo的div中間*/
  padding: 0 28px 0 0; /*左右padding*/
}

#mainInfo > ul li > div{
  width: 30px; /*圖片寬度*/
  height: 30px; /*圖片高度*/
  display: inline-block; /*流動式的自動排版，才能使用width跟height*/
  position: relative; /*能改變相對位置*/
  top: 0.3em; /*高度置中*/
  margin: 0 0.1em; /*圖片跟文字之間有距離*/
  background-position: center; /*圖片置中*/
  background-repeat: no-repeat; /*圖片若太小不重複*/
  background-size: contain; /*讓圖片能scale*/
}

